

@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;700&display=swap');

:root{
    --peach:#CE8A6D;
}
footer{
    position: absolute;
    z-index: 99;
    margin-top: 314vh;
    width: 100%;
    overflow: hidden;
}

.footer-container {
    width: 100%;
    background-color: #050505;
    display: flex;
    flex-direction: column;
    justify-content: space-around; 
    align-items: center; 
    padding: 20px; 
    flex-wrap: wrap; 
    font-family: 'Quicksand', sans-serif;
}

.footer-item {
    text-align: center;
    color: white;
    flex-basis: calc(33.33% - 20px); /* Set the initial width of each item */
    margin: 10px;
    order: 1; 
}

.footer-item p {
    font-size: 2vh;
}

.footer-item h1 {
    font-size: 4vh;
    color: var(--peach);
}

.footer-item img {
    width: 24vh;
}

.footer-item .social-icons,
.footer-item a {
    color: white;
}


/* Media query for screens between 320px and 48px */
@media (min-width: 320px) and (max-width: 480px) {
    .footer-container {
        flex-direction: column; 
    }

    .footer-item {
        flex-basis: 100%;
        margin: 2vh; 
    }
    /* .footer-item:nth-child(2){
        order: 0;
    }*/
    .footer-item p {
    font-size: 1.8vh;
    } 

    /* .footer-item h1 {
        font-size: 2vh;
    } */

    .footer-item img {
        width: 21vh;
    }
    .cop{
        font-size: smaller;
    }
}

/* Media query for screens between 481px and 768px */
@media (min-width: 481px) and (max-width: 768px){

    .footer-container {
        flex-direction: column; 
    }

    .footer-item {
        flex-basis: 100%; 
        order: 2; 
    }

    /* .footer-item:nth-child(2) {
        flex-basis: auto; 
        order: 1; 
    } */

    /* .footer-item:nth-child(1),
    .footer-item:nth-child(3) {
        flex-basis: 100%; 
    } */
}